.detail-about-section {
  .sub-section {
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    margin-top: 120px;

    @media (max-width: 1194px) {
      flex-direction: column;
      align-items: center;
    }

    @media (max-width: 768px) {
      margin-top: 60px;
    }

    .order-right {
      order: 1;

      @media (max-width: 1194px) {
        order: 2;
      }
    }

    .order-left {
      order: 2;

      @media (max-width: 1194px) {
        order: 1;
      }
    }

    .left {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 456px;
      width: 100%;

      @media (max-width: 1194px) {
        width: 100%;
        height: auto;
        margin-left: 0 !important;
      }

      video {
        display: block;
        vertical-align: middle;
        z-index: 10;
        width: 100%;
        height: 100%;
        border-radius: 0.75rem;
        object-fit: cover;
      }

      .badge-img {
        width: 100%;
      }
    }

    .right {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      gap: 2.5rem;
      align-items: flex-start;
      pointer-events: auto;
      margin-top: 1rem;
      margin-left: 80px;
      opacity: 1;
      transition: 0.3s all ease-in-out;

      @media (max-width: 1194px) {
        margin-left: 0 !important;
      }

      .title {
        text-align: start;
        font-weight: 300;
        line-height: 1.625;
        width: 653px;

        @media (max-width: 1194px) {
          width: 100%;
        }

        h1 {
          font-size: 48px;
          font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas,
            Liberation Mono, Courier New, monospace;
          font-weight: inherit;
          color: var(--title-color);
        }

        p {
          color: var(--content-text) !important;
          margin-top: 2rem;
        }
      }

      .learn-btn {
        color: inherit;
        text-decoration: inherit;

        button {
          position: relative;
          width: 8rem;
          overflow: hidden;
          border-radius: 0.125rem;
          font-size: 15px;
          padding: 8px 0px;
          box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
          outline: 1px solid;
          outline-color: rgba(255, 255, 255, 0.5);
          outline-offset: 0px;
          text-shadow: none;
          transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1);
          border: 0 solid;

          &:hover {
            border: 1px solid;
            box-shadow: inset 0 0 20px rgba(255, 255, 255, 0.5),
              0 0 20px rgba(255, 255, 255, 0.2);
            outline-color: rgba(255, 255, 255, 0);
            outline-offset: 15px;
            text-shadow: 1px 1px 2px #427388;
          }
        }
      }
    }
  }
}
